@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mulish:wght@300&family=Open+Sans&display=swap");

html {
  overflow-y: scroll;
}

* {
  margin: 0;
  font-family: "Mulish", sans-serif;
}
/* NAVBAR */
.navbar {
  padding: 20px;
  margin: 0px auto;
  align-items: center;
  display: flex;
  border-bottom: 5px transparent #1f1f1f;
  background-color: #121212;
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.2);
}

.navbar h3 {
  color: #fafafa;
}

.navbar .links {
  margin-left: auto;
}

.navbar a {
  margin-left: 16px;
  padding: 6px;
  text-decoration: none;
  color: #fafafa;
  transition: all 0.42s ease-in-out;
}

.navbar a:hover {
  color: #a1b3c2;
}

/* FOOTER */

.footer {
  display: flex;
  padding: 1rem;
  justify-content: center;
  align-items: center;
  background-color: #121212;
}

.social {
  display: flex;
  flex: 1;
  flex-direction: row;
}

.social a {
  padding: 5px;
  color: #f2f4f3;
}

.social a:hover {
  color: #04a777;
  transition: color 0.35s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.footer .right-section {
}

.footer p {
  color: #fafafa;
}

/* HOME */

.home-header {
  height: 50vh;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 10px;
  background: #0f4e61;
}

.home-header h1 {
  color: #e9f1f7;
  font-size: xx-large;
}

.home-header mark {
  color: rgb(21, 197, 115);
  background-color: transparent;
}

.home-main {
  height: 50vh;
  align-items: center;
  justify-content: center;
  display: flex;
  background: #040f16;
}

.home-main h2 {
  padding: 4rem;
  color: #e9f1f7;
  font-size: xx-large;
}

.home-main mark {
  background-color: transparent;
  color: hotpink;
}

/* PORTOFOLIO */

.portofolio {
  background-color: #f2f4f3;
}

.portofolio-header {
  height: 30vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.portofolio-header h1 {
  font-size: xxx-large;
}

.portofolio-projects {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}

.project {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.portofolio-projects h1 {
  margin-block: auto;
}

.portofolio-projects a {
  text-decoration: none;
  color: #f2f4f3;
  border-radius: 10px;
  background-color: #1a1a1a;
  box-shadow: 0 0 0 1px #1a1a1a;
  padding: 2px 5px 2px 5px;
  margin-bottom: 5%;
}

/* PROJECT in PORTOFOLIO*/
.project {
  height: 50vh;
  flex: 40%;
  text-align: center;
  margin-bottom: 40px;
  margin-inline: 40px;
  border-radius: 8px;
  box-shadow: 0 0 0 1px #a6a8aa;
  transition: all 430ms cubic-bezier(0.4, 0, 0.2, 1);
}

.project:hover {
  background-color: #2a4494;
  color: #fff;
  transform: scale(1.05);
  border-radius: 15px;
}

.project-image {
  width: 30%;
  padding: 10px;
  border-radius: 20%;
  padding: 10px;
}

.project-link {
  margin-top: auto;
}

.project-details {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.project-content {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}

.project-links {
  flex: 10%;
  display: flex;
  justify-content: center;
}

.project-title {
  padding: 20px;
}

.project-text {
  display: flex;
  flex-direction: column;
  padding: 3em;
  flex: 40%;
}

.project-features {
  margin-top: 2em;
  margin-bottom: 2em;
}

.feature {
  padding-top: 10px;
  font-size: large;
}

.description {
  padding-top: 10px;
  word-spacing: 2px;
  font-size: large;
}

.sliderimg {
  object-fit: fill;
}
